import { assign } from '../util/ObjectUtils';

const authReducer = (state = { logged: false }, action) => {
  const { type } = action;
  switch (type) {
    case 'REQUEST_REGISTER_PAGE':
      return assign(state, { logged: false });
    case 'SHOW_USER_MENU':
      return assign(state, { logged: true });
    default:
      return assign(state);
  }
};

export default authReducer;
